body {
  font-family: sans-serif;
}

* {
  box-sizing: border-box;
}

.comment {
  width: calc(100% - 4rem);
  align-items: center;
  margin-left: 4rem;
  display: flex;
}

.comment p {
  flex-grow: 1;
  gap: .25rem;
  display: block;
}

.comment .author {
  flex-direction: column;
  align-items: center;
  margin-right: 1rem;
  display: flex;
}

.pull-request {
  margin: 1rem;
  padding: 1rem;
  box-shadow: 0 0 5px 5px #d3d3d3;
}

.pull-request-heading {
  align-items: center;
  margin-bottom: 1rem;
  display: flex;
}

.pull-request img {
  margin-right: 1rem;
}

.pull-request h2 {
  margin-top: 0;
  margin-bottom: 0;
}

.comment .author img, .pull-request img {
  width: 3rem;
  height: 3rem;
  border-radius: 2rem;
  flex-shrink: 0;
}

.comment a {
  color: #fff;
  width: 10rem;
  text-align: center;
  background-color: green;
  border-radius: .25rem;
  flex-shrink: 0;
  margin: .25rem;
  padding: .25rem;
  text-decoration: none;
}

.comment a:hover {
  background-color: red;
}

label {
  width: 100%;
  justify-content: space-between;
  display: flex;
}

label input, label select {
  width: 75%;
}

input, select {
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #000;
  padding: .5rem;
}

